import { h } from 'preact';
import BarChart from './../../universal/BarChart/index.jsx';
import Download from './../../universal/Download/index.jsx';


export default function ProgrammesChart(props) {
  const {
    values,
    hasNull,
    year,
    files,
    width,
    mobile,
  } = props;

  const { parentAction, downloadAction } = props;

  const noValues = (
    <ul className="u-margin0 u-paddingLeft20">
      {Object.keys(values).map(val => <li>{val}</li>)}
    </ul>
  );

  const withValues = (
    <BarChart
      name="programmes-chart"
      guides={!mobile}
      hover={!mobile}
      {...{ width, parentAction, values }}
    />
  );

  return (
    <div className="Page-sectionWrap Page-sectionWrap--card Page-sectionWrap--dark">
      <div className="ProgrammesChart">
        <div className="ProgrammesChart-info">
          <div className="Page-section">
            <div>asdasd</div>
          </div>
        </div>
        <div className="ProgrammesChart-chart">
          <div className="Page-section Page-section--card">
            {hasNull ? noValues : withValues}
          </div>
        </div>
      </div>
    </div>
  );
}



